import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import  styled, { css } from 'styled-components'


class App extends Component {
  constructor(props){
    super(props)
    this.HOUSES = [
      {
        'name':'Glenroy Street',
        'features':'lorem',
        'description':'ipsum',
        'rent': 350,
        'available_date': '01/07/19',
        'bedrooms': 7,
      },
      {
        'name':'Mackintosh Place',

      }
    ]
    this.state = {
      current_house: "Glenroy Street"
    }
  }

  handleClick = (e) => { 
    var current_house = e.target.innerHTML
    this.setState({current_house})
  }
  render() {
    return (
      <React.Fragment>
      <NavBar houses={this.HOUSES} handleClick={this.handleClick}/>
      <House name={this.state.current_house} />
      </React.Fragment>
    );
  }
}


class NavBar extends Component {
  constructor(props) {
    super(props)

    this.state = {
      navLinksVisible: false,
    }
  }

  handleMenuClick = (e) => {
    this.setState(prevState => ({
      navLinksVisible: !prevState.navLinksVisible
    }))
  }


  render() {
    return (
      <React.Fragment>
      <NavBarStyled>
        <MenuIcon onClick={this.handleMenuClick}>
        â˜°
        </MenuIcon>
        <NavTitle>
        Rhys and Tony houses
        </NavTitle>
      </NavBarStyled>
      <NavLinks visible={this.state.navLinksVisible} houses={this.props.houses} handleClick={this.props.handleClick}/>
      </React.Fragment>

    )
  }
}

const NavBarStyled = styled.div``
const MenuIcon = styled.div`
  display: inline-block;
`
const NavTitle = styled.div`
  display: inline-block;
`


class NavLinks extends Component {
  constructor(props){
    super(props)

  }


  render() {
    return (
      <NavLinksStyled>
            
      {this.props.visible && this.props.houses.map((house,i) => (
        <a onClick = {this.props.handleClick} href="#" value={house.name} key={i}> {house.name} </a>
        ))}
      </NavLinksStyled>
    )
  }
}

const NavLinksStyled = styled.div``



class House extends Component {
  constructor(props) {
    super(props)
    this.HOUSE_LINKS = [
      {
        'href':'#',
        'name':'About',
      },
      {
        'href':'#',
        'name':'Info',
      },
      {
        'href':'#',
        'name':'Pictures',
      },
    ]

    this.state = {
      current_section : 'About'
    }
  }

  handleClick = (e) => {
    e.stopPropagation()
    var current_section = e.target.innerHTML
    this.setState({current_section})
  }

  render() {
    return (
      <React.Fragment>
      <p>{this.props.name}</p>
      <InfoLinks sections={this.HOUSE_LINKS} handleClick={this.handleClick}/>
      <Section name={this.props.name} section={this.state.current_section}/>
      </React.Fragment>
    )
  }
}

const HouseStyled = styled.a``

class InfoLinks extends Component {
  constructor(props) {
    super(props);
  }


  render() {
    return (
      <React.Fragment>
        {this.props.sections.map((house, j) => (
          <Link Ikey={j} name={house.name} handleClick={this.props.handleClick} />
        ))}
      </React.Fragment>
    )
  }
}

class Link extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <a href="#" key={this.props.Ikey} onClick={this.props.handleClick}> {this.props.name} </a>
    )
  }
}

class Section extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return(
      <p> {this.props.name}, {this.props.section} </p>
    )
  }
}





const Button = styled.button`
  background: transparent;
  border-radius: 3px;
  border: 2px solid palevioletred;
  color: palevioletred;
  margin: 0 1em;
  padding: 0.25em 1em;

  ${props =>
    props.primary &&
    css`
      background: palevioletred;
      color: white;
    `};
`

export default App;